import React, { Component } from 'react';
import './slider.style.scss';

interface SliderComponentProps {
  theme: string;
  disabled: boolean;
  onSendProgress: (value: number) => void; // Изменено: value - number
}

export class SliderComponent extends Component<SliderComponentProps> {
  scrollbarRef = React.createRef<HTMLInputElement>();
  scrollTrackRef = React.createRef<HTMLDivElement>();

  componentDidMount() {
    this.updateScrollTrack();
  }

  componentDidUpdate(prevProps: Readonly<SliderComponentProps>) {
    if (this.props.onSendProgress !== prevProps.onSendProgress) {
      this.updateScrollTrack();
    }
  }


  updateScrollTrack = () => {
    const { onSendProgress } = this.props;
    if (onSendProgress && typeof onSendProgress === 'function' && this.scrollTrackRef.current && this.scrollbarRef.current) {
      const value = this.scrollbarRef.current.value;
      this.scrollTrackRef.current.style.width = value + '%';
      onSendProgress(parseInt(value, 10)); // Передаем числовое значение
    }
  };

  onInput = () => {
    this.updateScrollTrack();
  };

  render() {
    const { theme, disabled } = this.props;
    const classes = disabled
      ? `slider--${theme} slider--disabled--${theme}`
      : `slider--${theme}`;

    return (
      <div className={`slider-container ${classes}`}>
        <input
          ref={this.scrollbarRef}
          type="range"
          className="slider"
          onInput={this.onInput}
          disabled={disabled}
        />
        <div ref={this.scrollTrackRef} className="scroll-div"></div>
      </div>
    );
  }
}

export default SliderComponent;